import "./scss/ShopButton.scss";
import { IS_THEME } from "../../../../utils";
const ShopButton = ({ ThemeRule, btnClass }) => {
  const shopBtnStyle = { fontFamily: ThemeRule.css_font }
  return <div className={`tb_shop_btn_wrap ${btnClass}`}>
    <div className={`tb_shop_btn${ThemeRule.postHover || IS_THEME(4) || IS_THEME(55) ? ' tb_shop_btn_light' : ' tb_shop_btn_dark'}`} style={shopBtnStyle}>
      {ThemeRule.shoppingText}
    </div>
  </div>
}


export default ShopButton;