import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
import Network from "../../Elements/Network/Network";
import { isTagEmbed, isEmbedTag } from "../../../../actions/api";
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font }) => {
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;

  return (
    <div className={`tb_hs_author_wrapper ${isTagEmbed ? 'tb_hs_author_wrapper_top' : ''}`} aria-label="Author details" aria-roledescription="post author details">
      <div className="tb_hs_author">

        {postAuthor ? <Author network={network} author={author} authorClass={'tb_hs_author_profile'} /> : ''}
        <div className="tb_hs_author_info">
          {postAuthor ? <div className="tb_hs_authorname">{author.name}</div> : ''}
          <div className="tb_hs_post_info">
            {postAuthor ? <div className="tb_hs_username">{`@${author.username}`}</div> : ''}
            {authorSeprator ? <div className="tb_hs_seprator"> </div> : ''}
            {personalization.postTime ? <Time postTime={postTime} timeClass={`tb_hs_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /> : ''}
          </div>
        </div>
      </div>
      {!isEmbedTag ?
        <div className="tb_hs_social_">
          <Network networkClass={'tb_hs_social__ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} />
        </div>
        : null
      }
    </div>
  );
}
export default AuthorDetails