import React, { Suspense } from "react";
import { isTagEmbed, isTagShop, isTagbox } from "../../../../actions/api";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";

const Media = ({ itemData, wallID, ThemeID, ThemeRule, ownerId, wall }) => {
  const autoPlayTagEmbed = isTagEmbed && (itemData.type === 3 || itemData.type === 5) && !wall.Personalization.mobilePopup && !wall.Personalization.postFeatured ? true : false
  const size = isTagEmbed || isTagbox ? wall.ThemeRule.aspectImageRatio ? wall.ThemeRule.aspectImageRatio : 100 : 100
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && itemData.network.id !== 28 && wall.ThemeRule.autoPlay == 1 ? true : false
  const mediaIcon = isTagShop && itemData.hotspot ? false : true
  return <div className="tb_hs_media_wrap" role="button" tabindex="0" aria-label="Pop Up Button">
    {!autoPlayTagEmbed ? <MediaIcon itemData={itemData} IconClass="tb_hs_media_icon" show={mediaIcon} /> : null}
    {autoPlay ?
      <Video VideoClass="tb_hs_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} isCover={true} muted={true} autoPlay={autoPlay} handleVideoEnded={() => { }} />
      :
      <Image ImageClass="tb_hs_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} ThemeRule={ThemeRule} hotspot={true} />
    }
  </div>
}
export default Media