import React, { PureComponent } from "react";
import { getImageSize } from 'react-image-size';
import ReactPlayer from "react-player"
import { POPUP_VIDEO_RENEW } from '../../../../actions/themeActions'
import { isTagShop, isTagEmbed } from "../../../../actions/api";
import PostBranding from "../Branding/PostBranding";

const mediaReplaceText = (mediaUrl, networkId) => {
    switch (networkId) {
        case 25:
            const soundCloud = String(mediaUrl).replace("https://w.soundcloud.com/player/?url=", "")
            return soundCloud
            break;

        default:
            return mediaUrl
            break;
    }

}
export default class Video extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            loadError: false,
            paddingBottom: 100,
            videoLoaded: false,
            imgUrl: this.props.data.postFileNew,
            isVisible: false,
        }
        this.videoRef = React.createRef();
        this.observer = null;
    }
    componentWillMount() {
        const { data } = this.props;
        let updateVideo = document.querySelector(`#video_p_${data.id}`);
        if (updateVideo) {
            updateVideo.play()
            updateVideo.muted = true;
        }

        if (this.props.size) {
            this.setState({
                paddingBottom: this.props.size == 1 ? null : isTagEmbed ? this.props.size : this.props.size
            })
        } else {
            this.onMediaLoad();
        }
    }

    componentDidMount() {
        const { data } = this.props;
        let updateVideo = document.querySelector(`#video_p_${data.id}`);
        if (updateVideo) {
            updateVideo.play()
            updateVideo.muted = true;
        }

        if (this.props.size) {
            this.setState({
                paddingBottom: this.props.size == 1 ? null : isTagEmbed ? this.props.size : this.props.size
            })
        } else {
            this.onMediaLoad();
        }
        try {
            this.observer = new IntersectionObserver(
                ([entry]) => {
                    this.setState({ isVisible: entry.isIntersecting });
                },
                {
                    threshold: 0.5, // Video is considered visible if 50% of it is in the viewport
                }
            );

            if (this.videoRef.current) this.observer.observe(this.videoRef.current);
        }
        catch (error) {
            this.setState({ isVisible: true });
        }
    }

    onMediaLoad = async (props) => {
        const { data } = this.props;
        if (data && Object.keys(data).length > 0 && [4, 5, 3].includes(data.type)) {
            if (data.mediaHeight && data.mediaHeight != "" && data.mediaWidth && data.mediaWidth != "") {
                this.setState({
                    paddingBottom: (data.mediaHeight * 100) / data.mediaWidth
                })
            } else {
                try {
                    const { width, height } = await getImageSize(data.postFileNew);
                    this.setState({
                        paddingBottom: (height * 100) / width
                    })
                } catch (error) {
                    this.setState({
                        paddingBottom: 100
                    })
                }
            }
        }
    }
    setVideoLoaded = event => {
        this.setState({
            videoLoaded: event
        })
    }

    componentWillUnmount() {
        if (this.observer && this.videoRef.current) {
            this.observer.unobserve(this.videoRef.current);
        }
    }
    render() {
        const { VideoClass, data, wallID, isCover, controls, autoPlay, handleVideoEnded, muted } = this.props
        const { paddingBottom, videoLoaded, isVisible } = this.state
        const videoStyle = {
            paddingBottom: `${paddingBottom}%`
        };
        const mediaClip = data.mediaClip && String(data.mediaClip).length > 0 ? data.mediaClip : data.mediaUrl
        return <div className={`${VideoClass}_wrap_ mediaHolder${data.id}`} style={videoStyle} ref={this.videoRef}>
            {/* {data?.postBranding ? <PostBranding size={1} /> : null} */}
            <ReactPlayer
                // ref={videoRef}
                className={`${VideoClass} video${data.id}`}
                url={mediaReplaceText(mediaClip, data.network.id)}
                data-type="video"
                data-network={data.network.id}
                data-link={data.link}
                data-wall-id={wallID}
                data-item-id={data.id}
                data-feed-id={data.feedId ? data.feedId : ""}
                data-load={0}
                config={{
                    facebook: {
                        appId: '117029975354072',
                        attributes: {
                            'data-width': 'auto',
                            'data-height': `${document.querySelector('.ts_rt_post_in') ? `${document.querySelector('.ts_rt_post_in').clientHeight} px` : '100%'}`,

                        }
                    },
                    file: {
                        attributes: {
                            preload: 'metadata',
                            // onError: (e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e)
                        }
                    }
                }}
                loop={autoPlay ? true : true}
                onError={(e) => {
                    if (!data.stories || data.stories == 0) POPUP_VIDEO_RENEW(e, data.id)

                    //this.setVideoLoaded(false)
                }}
                //(data.stories == 0) ? POPUP_VIDEO_RENEW(e) : null}
                autoPlay={autoPlay}
                muted={muted}
                volume={autoPlay ? 1 : 0}
                playsinline={true}
                onReady={(e) => { isTagShop ? this.setVideoLoaded(false) : this.setVideoLoaded(true) }}
                playing={isTagShop ? isVisible : autoPlay}
                height={'100%'}
                width={'100%'}
                controls={controls ? controls : false}
                // onProgress={onPlayingVideo}
                style={{ backgroundImage: (!videoLoaded) ? `url(${data.postFileNew})` : `url(${data.postFileNew})`, backgroundSize: isCover ? 'cover' : 'contain', backgroundPosition: 'center', backgroundRepeat: 'no-repeat' }}
                onEnded={handleVideoEnded ? handleVideoEnded : null}
            />
        </div>
    }
}



