import React, { PureComponent } from "react";
import { themePostTracking, gtmAnalyticsAction } from '../../../../actions/themeActions'
import { isTagEmbed } from '../../../../actions/api'

export default class ShareOnHover extends PureComponent {


  onClickData = (shareURL) => event => {
    const { item, wallID, ownerId } = this.props;
    event.stopPropagation();
    if (!isTagEmbed) themePostTracking({
      type: 2,
      action: 2,
      wall: wallID,
      feed: item.feedId,
      post: item.referenceId ? item.referenceId : item.id,
      owner: ownerId
    })
    gtmAnalyticsAction("Tagbox", "shareUrl", "shareUrl", shareURL)
    window.open(shareURL, "_blank")
  }

  render() {
    const { share, shareClass } = this.props;
    return <div className={`tb_share_wrapper ${shareClass}`}>
      <div className={`tb_share_button_ tb__icon ${isTagEmbed ? 'tb-share-fill' : 'tb-share'}`}><div></div></div>
      <div className="tb_share_icon_list_wrap">
        {share.facebook ?
          <div className="tb_share_icon_list">
            <div onClick={this.onClickData(share.facebook)} className="tb_share_ico__ tb__icon tb-facebook" title="facebook"><div></div></div>
          </div>
          : ''}
        {share.twitter ?
          <div className="tb_share_icon_list">
            <div onClick={this.onClickData(share.twitter)} className="tb_share_ico__  tb__icon tb-twitter" title="twitter"><div></div></div>
          </div>
          : ''}
        {share.linkedin ?
          <div className="tb_share_icon_list">
            <div onClick={this.onClickData(share.linkedin)} className="tb_share_ico__ tb__icon tb-linkedin" title="linkedin"><div></div></div>
          </div>
          : ''}
      </div>
    </div>
  }
}
