import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import Card from "./Elements/Card";
import { getDataNextSteps } from '../../../actions/themeActions'
import '../scss/ThemesSplide.scss'
import './scss/HorizontalSlider.scss'
import { isTagEmbed, isTagShop } from "../../../actions/api"
class HorizontalSlider extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { parent_id: '', postData: [] }
    this.inputRefs = {};
  }
  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element, index) => {
        element.removeAttribute("role");
      });
    }, 100);
    const { postData, wall } = this.props
    this.setState({ postData })
    window.addEventListener('keydown', this.handleKeyPress);
  }
  handleKeyPress = event => {
    try {
      const { wall } = this.props
      if (wall.ThemeRule.slidePost == 1) {
        if (event.code === 'Space') {
          const { parentID } = this.props
          event.preventDefault(); // Prevent scrolling the page
          var Autoplay = this.inputRefs[parentID].splide.Components.Autoplay;
          if (Autoplay.isPaused()) Autoplay.play();
          else Autoplay.pause();
        }
      }
    }
    catch (error) {
    }
  }
  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleKeyPress);
  }
  requestData = () => {
    const { appendData, wall, preRender, hasMoreData, getDataNextSteps } = this.props;
    if (hasMoreData) {
      getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  autoScrollSlider = (splide, prev, next, destIndex) => {
    const { wall } = this.props
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    if (autoScrollStatus == 1 || isTagShop) {
      const currentIndex = isTagEmbed ? (splide["_o"].perPage + splide.index) : splide.index
      const slideLength = splide.length
      const permove = isTagEmbed ? 1 : splide["_o"].perPage
      if (slideLength - currentIndex === permove) this.requestData()
    }
  }
  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, onClickToCTA, postSizeInPx, parentID, loaderData, onClickPopUpSlider } = this.props;
    const inputName = parentID;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    const rowCount = !wall.ThemeRule.multiRow || wall.ThemeRule.multiRow === 0 ? 1 : wall.ThemeRule.multiRow;
    var newPostdata = []
    let isVisible = true
    const cloneStatus = (wall?.User?.userDatabaseType == "tagembed" || isTagEmbed || isTagShop) && wall.Personalization?.clonePost !== undefined ? wall.Personalization.clonePost === 0 ? { clones: 0 } : {} : {};


    var i = 0, n = postData.length;
    while (i < n) { newPostdata.push(postData.slice(i, i += rowCount)); }



    if (isTagShop && cloneStatus && Object.keys(cloneStatus).length == 0) {
      if (wall.User.id == 16677) {
        newPostdata = newPostdata
        if (window.dataFilterType === 'product') isVisible = newPostdata && newPostdata.length >= 5 ? true : false
      }
      else newPostdata = newPostdata.length < 12 ? Array(15 - newPostdata.length).fill(newPostdata).flat() : newPostdata;
    }

    const isType = autoScrollStatus != 1 ? Object.keys(cloneStatus).length > 0 ? false : (wall?.User?.userDatabaseType == "tagembed" || isTagEmbed ? 'slide' : 'loop') : (isTagShop ? 'slide' : 'loop')
    const settings = {
      ...cloneStatus,
      type: autoScrollStatus === 1 ? false : isType,
      rewind: true,
      speed: 1000,
      interval: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : wall?.User?.userDatabaseType == "tagembed" || isTagEmbed ? 3 : 6,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: 0,
      pagination: false,
      arrows: true,
      breakpoints: {
        560: {
          perPage: mobileColumn > 0 ? mobileColumn : 1,
        },
        767: {
          perPage: mobileColumn > 0 ? mobileColumn : 2,
        },
        991: {
          perPage: mobileColumn > 0 ? mobileColumn : 3,
        },
        1200: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 4,
        },
        1400: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 5,
        },
        1600: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : isTagEmbed ? 3 : 6,
        }
      },
    };
    return (<>{parentID != "" ? <div className="tb_hs_post_container">
      {isVisible ? <Splide hasTrack={false} className="tb_hs_post_slider" options={settings} ref={(parentID) => this.inputRefs[inputName] = parentID} onMove={this.autoScrollSlider}>
        <SplideTrack>
          {
            (wall.ThemeRule.multiRow > 1 && newPostdata.length !== 1 && newPostdata.length % 2 !== 0 ? newPostdata.pop() : newPostdata) && ((newPostdata && newPostdata.length > 0)) && newPostdata.map((item, index) => {


              return <SplideSlide key={`tb_${index}_${index}`} className={`tb_${index}`} idd={parentID}>
                <div className="tb_hs_col_wrap">
                  {
                    item.map((subItem, subIndex) => {
                      const cardData = completeDataObject[subItem];
                      return <Card key={subIndex} parentID={parentID} ownerId={wall.Wall.owner} wall={wall} itemData={cardData} itemIndex={subIndex} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} />
                    })
                  }
                </div>

              </SplideSlide>
            })}
        </SplideTrack>
        <div className="splide__arrows splide__arrows--ltr tb_hs_arrow_wrapper_">
          <div className="tb_hs_arrow splide__arrow splide__arrow--prev tb_hs_arrow tb_hs_arrow_left__ tb__icon tb-arrow-left-alt" role="button" aria-label="Go to previous slide" ><div></div></div>
          <div className={`tb_hs_arrow splide__arrow splide__arrow--next  tb_hs_arrow_right__ tb__icon tb-arrow-right-alt ${loaderData.isShowMoreLoading ? `tb_a_spinner` : ``}`} onClick={isTagShop || isTagEmbed ? event => this.requestData() : null} role="button" aria-label="Go to next slide" ><div></div></div>
        </div>
      </Splide> : null}
    </div> : null}</>)
  }
}

const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(HorizontalSlider);