import { useEffect, useRef } from 'react'
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import { PostTrimContent, themeImpressionTracking } from '../../../../utils'
import Network from "../../Elements/Network/Network";
import ShopButton from "../../Elements/ShopButton/ShopButton";
import { isTagEmbed, isTagShop, isTagbox } from "../../../../actions/api";
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover"

const trim_content_length = !isTagEmbed ? 200 : null;
const Card = ({ itemData, personalization, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, postSizeInPx, wall, isPadding, isRowCount, onClickPopUpSlider }) => {
  const postTrack = useRef(null);
  const isProduct = itemData.ugc_products && itemData.ugc_products.length > 0 ? true : false
  const isShopIcon = (isProduct && itemData.hotspot === 0) ? true : false
  const isTagshopHover = ThemeRule.postHover === "" || ThemeRule.postHover !== 1
  const mediaType = itemData.type !== 1 ? true : false;
  const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
  const responsiveCardSize2x = postSizeInPx.adjustWidth < 250 ? true : false
  const isHotspot = itemData.hotspot && isProduct ? true : false
  const isShopBtn = isTagShop && (!itemData.hotspot && isProduct) ? true : false;
  const ctaStatus = !ThemeRule.hideContent;
  const isContent = isShopIcon ? ctaStatus == 0 ? true : false : ThemeRule.hideContent == 1 ? false : true
  const shopBtn = isShopIcon ? ctaStatus == 0 ? false : true : false
  const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'
  const overlay = isTagbox ? 'tb_hs_color_overlay' : 'tb_hs_post_overlay-' + themeMode
  const overlayStyle = { backgroundColor: isTagbox ? ThemeRule.cardColor : '' }
  useEffect(() => {
    if (isTagShop) themeImpressionTracking(postTrack);
  }, []);
  useEffect(() => {
    if (itemData.isPopUp) onClickPopUpSlider(itemIndex, itemData)
  }, [itemIndex, itemData.isPopUp, itemData]);
  return (
    <div id={`tb-hs-post-${itemData.id}`} className={`tb_hs_post_wrapper${responsiveCardSize2x ? ' tb_hs_post_wrapper2x' : ''}`} tb-network={itemData.network.id} tb-product={isShopIcon ? 1 : isHotspot ? 2 : 0} style={{ padding: personalization.padding / 2 }} ref={postTrack} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} role="article" aria-label={`Post ${parseInt(itemIndex) + 1},${postTextContent}`} >

      <div className={`tb_hs_post_in${isTagShop ? ' tb_hs_post_ani' : ''}`} onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-roledescription="Click to open pop up" tabIndex={"0"}>
        <div className="tb_hs_post_media_wrapp" role="button" tabindex="0" aria-label="Pop Up Button">
          {mediaType ? <Media key={itemData.id} itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} /> : ''}
          {!isHotspot ? <>
            <div className="tb_hs_post_details">
              <div className="tb_hs_contant_wrapper">
                {isTagShop ? <div className="tb_hs_social_">
                  <Network networkClass={'tb_hs_social__ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} Personalization={personalization} />
                </div> : null}
                {shopBtn ? <ShopButton ThemeRule={ThemeRule} btnClass={'tb_hs_btn_wrap'} /> : null}
                {(isContent || itemData.type == 1) ? <PostContent contentClass={'tb_hs_content'} item={itemData} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} isNetwork={true} /> : null}
              </div>
              {!isTagShop ?
                <AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />
                : null}
              {itemData.share.status ? <ShareOnHover share={itemData.share} shareClass={`tb_hs_share_container ${isTagEmbed ? 'tb_hs_share_container_te' : ''}`} item={itemData} wallID={wallID} ownerId={ownerId} /> : null}
            </div>
            {!isHotspot ? <div className={`tb_hs_post_overlay ${overlay}`} style={overlayStyle}><div></div></div> : null}
            {isTagEmbed ?
              <div className="tb_hs_social_bottom_">
                <Network networkClass={'tb_hs_social__ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} Personalization={personalization} />
              </div>
              : null}
          </> : null}
        </div>
      </div>
    </div>
  );
}
export default Card