import React, { PureComponent, Suspense } from "react";
import ReactHtmlParser from 'react-html-parser';
import { IS_THEME, STRING_TO_URL_CONVERT } from '../../../../utils'
import { isTagEmbed, isTagShop } from "../../../../actions/api";
import Network from "../../Elements/Network/Network";
const ContentWithOutSlack = React.lazy(() => import('./PostContentConvertion'));
export default class PostContent extends PureComponent {
  constructor(props) {
    super(props);
    // this.state = {
    //   expanded: false,
    // };

    this.contentRef = React.createRef();
  }
  // toggleExpanded = event => {
  //   this.setState({
  //     expanded: !this.state.expanded,
  //   });
  //   event.stopPropagation()
  // }

  componentDidMount() {
    setTimeout(() => {
      if (this.contentRef?.current?.clientHeight) {
       const contentHeight = this.contentRef?.current?.clientHeight || 0;
        if (this.props.contentRefCallBack) this.props.contentRefCallBack(contentHeight)
      }
    }, 100);
  }


  render() {
    const { contentClass, content, ThemeRule, font, personalization, item, contentTitle, readMore, maxLength, textAlign, maxLines, isReadMore } = this.props;
    //const { expanded } = this.state
    const truncateStyle = {
      WebkitLineClamp: maxLines,
      textAlign: ThemeRule.textAlignment
    };
    const textStyle = {
      textAlign: textAlign ? textAlign : ThemeRule.textAlignment
    }
    //isTagEmbed && !IS_THEME(59) ? font.fontColor : null
    let contentData = item.network.id == 20 ? content : STRING_TO_URL_CONVERT(content);

    if (item.network.id == 12) contentData = ReactHtmlParser(contentData); // for RRS but we have to change from backend.

    //const text = readMore ? expanded ? contentData : contentData.slice(0, maxLength) : contentData;

    const trimClasses = `${ThemeRule.lineTrim ? ` tb_content_line-${ThemeRule.lineTrim}` : ``}`
    const contentClasses = `${contentClass} ${!isReadMore ? trimClasses : ''} tb-cTBfont-${ThemeRule.font_varient}`
    return (
      <div className={contentClasses} ref={this.contentRef} style={truncateStyle}>
        {contentTitle ? <div className="tb_bold_txt__">{ReactHtmlParser(contentTitle)}</div> : null}
        <Suspense fallback={<></>}> <ContentWithOutSlack data={item} content={contentData} Personalization={personalization} />
        </Suspense>
      </div>
    );
  }
}
