import React, { memo, useMemo } from "react";
import { timeAgo } from '../../../../utils'
const Time = (props) => {
  const { postTime, timeClass } = props;
  return <div className={timeClass} role="status" aria-label={`post timestamp ${timeAgo(postTime)}`}>{timeAgo(postTime)}</div>
}

export default memo(Time)